  mounted() {},
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    submit() {
      let that = this;
      this.params.question = question;
      axios.post("/user/register", this.params).then(res => {
        console.log(res);
        if (res.data.code === 3001) {
          this.store.userInfo = res.data.data;
          // sessionStorage.setItem("userInfo",JSON.stringify(res.data.data))
          showSuccessToast(res.data.msg);
          // console.log("session"+JSON.stringify(res.data.data))
          // this.store.isLogin=true
          // 
          setTimeout(function () {
            console.log("");
            that.$router.push("/login");
          }, 2000);
        } else {
          showFailToast(res.data.msg);
        }
      });
      console.log(this.params);
    },
    beforeRead(file) {
      // jpgpnggif
      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isGIF = file.type === 'image/gif';
      console.log("" + file.type);
      const isLt1M = file.size / 1024 / 1024 < 1;
      if (!isJPG && !isPNG && !isGIF) {
        showFailToast(' JPG/PNG/GIF !');
      }
      if (!isLt1M) {
        showFailToast(' 1MB!');
      }
      return isLt1M;
    },
    afterRead(file) {
      let that = this;
      // 
      console.log(file);

      //FormData
      // FormData 
      // 1.formnamevaluequeryString
      // 2. 
      //FormData
      const forms = new FormData();
      //file
      forms.append("file", file.file); // 

      //
      //url
      //headerstokentoken
      //token
      axios.post("/user/upload", forms, '', {
        headers: {
          "content-type": "multipart/form-data"
        }
      }).then(res => {
        //200
        if (res.data.code === 1001) {
          console.log("!!!!!!!!!!!!!!!!!!!");
          console.log(res);
          that.params.avatar = res.data.data.filePath;
          console.log(">>>>>>>" + that.params.avatar);
          //this.imageData = res.data.showapi_res_body;
          //this.isShow = false;
          //this.showList = true;
          //Toast(res.data.showapi_res_body.remark);
        } else {
          //Toast(res.data.msg);
          //this.isShow = false;
          showFailToast(res.data.msg); //
        }
      });
    }

    // onConfirm(columns) {
    //   this.params.question = this.$refs.pickerRef.title;
    //   this.showPicker = false;
    //
    //   //   columns[0].text
    // },
